<pre><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">zan-button</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"handleAlertClick"</span>&gt;</span>alert<span class="hljs-tag">&lt;/<span class="hljs-name">zan-button</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">zan-button</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"handleConfirmClick"</span>&gt;</span>confirm<span class="hljs-tag">&lt;/<span class="hljs-name">zan-button</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">import</span> { Dialog } <span class="hljs-keyword">from</span> <span class="hljs-string">'src/index'</span>;

<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  <span class="hljs-attr">methods</span>: {
    handleAlertClick() {
      Dialog.alert({
        <span class="hljs-attr">title</span>: <span class="hljs-string">'alert标题'</span>,
        <span class="hljs-attr">message</span>: <span class="hljs-string">'弹窗提示文字，左右始终距离边20PX，上下距离20PX，文字左对齐。弹窗提示文字，左右始终距离边20PX，上下距离20PX，文字左对齐。'</span>
      }).then(<span class="hljs-function">(<span class="hljs-params">action</span>) =&gt;</span> {
        <span class="hljs-built_in">console</span>.log(action);
      });
    },

    handleConfirmClick() {
      Dialog.confirm({
        <span class="hljs-attr">title</span>: <span class="hljs-string">'confirm标题'</span>,
        <span class="hljs-attr">message</span>: <span class="hljs-string">'弹窗提示文字，左右始终距离边20PX，上下距离20PX，文字左对齐。弹窗提示文字，左右始终距离边20PX，上下距离20PX，文字左对齐。'</span>
      }).then(<span class="hljs-function">(<span class="hljs-params">action</span>) =&gt;</span> {
